<template>
  <Card class="card">
     <Breadcrumb>
        <BreadcrumbItem>船东中心</BreadcrumbItem>
        <BreadcrumbItem>船东列表</BreadcrumbItem>
    </Breadcrumb>
      <div class='box-container'>
         <div style="margin:20px 0 40px 20px">
            <Input placeholder="请输入船东姓名" style='width:220px' v-model="name" @keyup.enter.native="search()" /> <Button type="primary" @click="search()">搜索</Button>
        </div>
        <div style="width:75%;margin-left:20px;">
         <Table border :columns="columns1" :data="data1"></Table>
        <Page :total="total" @on-change="changePage"   show-elevator show-sizer
        :current="currentPage" :page-size="size" @on-page-size-change='sizeChange'   class="pages"/>

        </div>
      </div>
  </Card>
</template>
<script>
    export default {
        data () {
            return {
                name:'',
                total:10,
                columns1: [
                     {
                        title: '#',
                        key: 'Index',
                         align:'center',
                         width:80
                    },
                    {
                        title: '船东姓名',
                        key: 'name',
                        align:'center',
                    },
                    {
                        title: '手机号',
                        key: 'phone',
                        align:'center',
                    },
                    {
                        title: '船舶数量',
                        key: 'shipCounter',
                        align:'center',
                    }, {
                        title: '详情',
                        key: 'detail',
                         align:'center',
                           width:120,
                         render: (h, params) => {
                           return h('Button', {
                             props:{
                               type:"primary"
                             },
                              style:{
                                display:'inline-block',
                                // width:100,
                                background:'red;'
                              },
                              on:{
                                click:()=>{
                                    let routeUrl = this.$router.resolve({
                                      path: "/cdCenter/cddetail",
                                      query: {code:params.row.code}
                                });
                                window.open(routeUrl .href, '_blank');

                                }
                              }
                           },'查看')
                       }
                    },
                ],
                data1: [],
                currentPage:1,
                size:10,
            }
        },
        watch:{
            name(){
              if(!this.name){
                 this.size =10
                 this.cdList()
                 localStorage.removeItem('searchInfo')
              }
            }
        },
        methods:{
        async  cdList(){
            let data = {
              currentPage:this.currentPage,
              size:this.size,
            }
               try{

                 let res = await this.$http.cdList(data)
                 if(0==res.data.status &&  res.data.result instanceof Array){
                     this.total = res.data.total
                     let result = res.data.result
                     this.data1 = []
                     this.commonFn(result)
                 }
                 else{
                   this.$Notice.error({
                     desc:'暂无数据'
                   })
                   this.data1 = []
                   this.total=0
                 }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
          },
        // 公共函数
        commonFn(result){
          result.forEach((item,index) => {
                    this.data1.push({
                          Index:Number((this.currentPage-1)*this.size)+Number(index)+1,
                          name: item.clientName,
                          phone: item.phone,
                          shipCounter: item.shipNum,
                          id:item.id,
                          code:item.code,
                          detail:'查看'
                    })
                  });
        },
        changePage(currentPage){
            this.currentPage = currentPage;
            if(localStorage.searchInfo){
                this.search(currentPage)
            }
            else{
              this.cdList()
            }
          },
          sizeChange(size){
              this.size = size
              if(!this.name){
                this.cdList()
              }
              else{
                this.search()
              }
          },

       // 查询
        async  search(currentPage){
          if(!this.name){
            this.$Notice.error({
              desc:"请输入船东姓名"
            })
            return false
          }
           currentPage ? this.currentPage = currentPage : this.currentPage =1
             let data = {
                  currentPage:this.currentPage,
                  size:this.size,
                  trem:this.name,
             }
             try{
                  let res = await this.$http.selectShipOwnerInfo(data)
                    this.total = res.data.total
                    this.data1 = []
                   if(!res.data.status &&  res.data.result instanceof Array){
                      localStorage.setItem('searchInfo',this.name)
                       this.$Notice.success({
                          desc:'查询成功'
                        })
                      let result = res.data.result
                      this.commonFn(result)
                   }
                   else{
                     this.$Notice.error({
                        desc:'暂无数据'
                      })
                      return false
                   }
             }
             catch(err){
               localStorage.removeItem('searchInfo')
               this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)
             }


          },
        },
        created(){
          this.cdList()
        },
        destroyed(){
           localStorage.removeItem('searchInfo')
        }
    }
</script>
<style lang="scss" scoped>
/deep/  .ivu-page{
  position: relative;
}
</style>



